import 'react-image-lightbox/style.css';

import AnchorLink from 'react-anchor-link-smooth-scroll'
import Breadcrumbs from '../components/Breadcrumbs'
import ContactOrder from '../components/ContactOrder'
import Footer from '../components/Footer'
import Header from '../components/Header'
import Lightbox from 'react-image-lightbox';
import React from 'react';
import TechTable from '../components/TechTable'
import axios from 'axios'
import {frontConfig} from '../settings/frontconfig'

class ProductDetail extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            location: window
                .location
                .pathname
                .slice(11, window.location.pathname.length)
                .replace('_', ' '),
            data: [],
            title: '',
            photoIndex: 0,
            isOpen: false,
        };
    }
    componentDidMount() {
        this.fetchData();
        this.findProductQuery()
    }
    fetchData = async() => {
        await axios
            .get(frontConfig.mainRestRef + frontConfig.mainJsonStream + frontConfig.getProducts)
            .then((result) => {
                this.setState({isLoaded: true, data: result.data});
            }, (error) => {
                this.setState({isLoaded: false, error});
            })
    }

    //Find product that matches our location, => output to html
    findProductQuery = (p) => {
        let wrap = ''
        if (window.innerWidth < 890) {
            console.log(window.innerWidth < 890)
            wrap = {
                flexWrap:'wrap !important'
            }
        }
        const {isLoaded, location} = this.state;
        if (isLoaded === true) {
            return p.map((item, i) => {
                let productNeeded = item
                    .title
                    .rendered
                    .toLowerCase();
                    
                if (location === productNeeded) {
                    const images = [
                        item.acf.picture,
                        item.acf.sub_foto_1,
                        item.acf.sub_foto_2,
                        item.acf.sub_foto_3
                      ];
                    const { photoIndex, isOpen } = this.state;
                    const splitKeywords = item.acf.zoekwoorden;
                    return (
                        
                        <section key={i} className="product-detail container mb-5">
                            <h1 className="my-3 w-100">{item.title.rendered}</h1>

                            <div className="d-flex wrapp flex-row mb-3">
                                <div>
                                    <img className="rounded img-fluid" style={{maxWidth:150+'px',maxHeight:175+'px'}} src={item.acf.picture} onClick={() => this.setState({ isOpen: true })}></img>
                                </div>
                                {/* ADD MAPPING HERE! */}
                                <div className="d-flex flex-column mx-3">
                                {isOpen && (
                                        <Lightbox
                                            mainSrc={images[photoIndex]}
                                            nextSrc={images[(photoIndex + 1) % images.length]}
                                            prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                                            onCloseRequest={() => this.setState({ isOpen: false })}
                                            onMovePrevRequest={() =>
                                            this.setState({
                                                photoIndex: (photoIndex + images.length - 1) % images.length,
                                            })
                                            }
                                            onMoveNextRequest={() =>
                                            this.setState({
                                                photoIndex: (photoIndex + 1) % images.length,
                                            })
                                            }
                                        />
                                        )}
                                    <img style={{ width: 50 + 'px' }} className="rounded mb-2" src={item.acf.sub_foto_1} onClick={() => this.setState({ isOpen: true })}></img>
                                    <img style={{ width: 50 + 'px' }} className="rounded mb-2"  src={item.acf.sub_foto_2} onClick={() => this.setState({ isOpen: true })}></img>
                                    <img style={{ width: 50 + 'px' }} className="rounded mb-2"  src={item.acf.sub_foto_3} onClick={() => this.setState({ isOpen: true })}></img>
                                </div>

                                <div className="d-flex flex-column  justify-content-around">
                                    <p>{item.acf.descr}</p>
                                    <div className="d-flex flex-column align-items-end">
                                        <span className="my-4">Richtprijs €{item.richtprijs_},00</span>
                                        <div className="d-flex justify-content-center align-items-end align-content-end">
                                            <AnchorLink  style={{backgroundColor:'#54A4DB'}} className="btn rounded font-weight-bold mx-1" href='#techTable'>Techniche fische</AnchorLink >
                                            <AnchorLink className="btn rounded font-weight-bold" href='#contactOrder'>Offerte</AnchorLink >
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </section>
                    )
                }

            })
        }
    }

    render() {
        const {data} = this.state;
        return (
            <div className="loaded">
                <Header/>
                <Breadcrumbs/> {this.findProductQuery(data)}
                <TechTable/>
                <ContactOrder/>
                <Footer/>
            </div>
        );
    }
}

export default ProductDetail;