import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import AnchorLink from 'react-anchor-link-smooth-scroll'
import React from "react";
import ScrollAnimation from 'react-animate-on-scroll';
import Slider from "react-slick";
import axios from 'axios'
import {frontConfig} from '../settings/frontconfig'
import headerVideo from '../assets/misc/bgwater.mp4'
import thumb from '../assets/img/thumb.jpg'

class SimpleSlider extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            isVideoLoaded:false,
            data: []
        };
    }

    fetch = async() => {
        await axios
            .get(frontConfig.mainRestRef + frontConfig.mainJsonStream + frontConfig.getSections)
            .then((result) => {
                this.setState({isLoaded: true, data: result.data[0].acf});
            }, (error) => {
                this.setState({isLoaded: false, error});
            })
    }

    componentDidMount() {
        this.fetch()
    }
    onLoadedData = () => {
        this.setState({
            isVideoLoaded:true
        })
      };
     Video = () => {
        return   (
        <div className="media-wrapper">
            <img src={thumb}  className="video-thumb tiny" alt="thumb" style={{ opacity: this.state.isVideoLoaded ? 0 : 1 }}/>
            <video playsInline muted autoPlay loop src={headerVideo}></video>
        </div>
        )
        }
    render() {
        const Video = () => {
        return   (
            <div className="media-wrapper">
                <img src={thumb}  className="video-thumb tiny" alt="thumb" style={{ opacity: this.state.isVideoLoaded ? 0 : 1 }}/>
                <video playsInline muted autoPlay loop src={headerVideo}></video>
            </div>
            )
        }

        const settings = {
            dots: true,
            infinite: true,
            speed: 800,
            autoplaySpeed: 16000,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: true,
            pauseOnFocus: true,
            pauseOnHover: false,
            pauseOnDotsHover: false,
            autoplay: true,
        };

        const {error, isLoaded, data} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return ('');
        } else {
            return <div className="slick-container">
                <Slider {...settings}>
                    <div className="slick-title-container">
                        <div className="headline text-center">
                            <ScrollAnimation animateIn="fadeInDown">
                                <div
                                    className="headline-sub d-flex flex-column justify-content-center align-items-center mb-5">
                                    <h3>{data.subtitle_nl_1}</h3>
                                    <h1>{data.title_nl_1}</h1>
                                </div>
                                <div>
                                    <a
                                        className="btn-header text-white border"
                                        href={data.button_1_href}>{data.button_nl_1}
                                    </a>
                                </div>
                            </ScrollAnimation>
                        </div>
                    </div>
                    <div className="slick-title-container">
                        <div className="headline text-center">
                            <ScrollAnimation animateIn="fadeInDown">
                                <div
                                    className="headline-sub d-flex flex-column justify-content-center align-items-center mb-5">
                                    <h3>{data.subtitle_nl_2}</h3>
                                    <h1>{data.title_nl_2}</h1>
                                </div>
                                <a
                                   
                                    className="btn-header text-white border my-5"
                                    href={data.button_2_href}>{data.button_nl_2}</a>
                            </ScrollAnimation>
                        </div>
                    </div>
                    <div className="slick-title-container">
                        <div className="headline text-center">
                            <ScrollAnimation animateIn="fadeInDown">
                                <div
                                    className="headline-sub d-flex flex-column justify-content-center align-items-center mb-5">
                                    <h3>{data.subtitle_nl_3}</h3>
                                    <h1>{data.title_nl_3}</h1>
                                </div>
                                <AnchorLink
                                    className="btn-header text-white border my-5"
                                    href={data.button_3_href}>{data.button_nl_3}</AnchorLink >
                            </ScrollAnimation>
                        </div>
                    </div>
                </Slider>
                
              
                    <Video/>
                 
               

            </div>
        }
    }
}
export default SimpleSlider;