import React from 'react';
import axios from 'axios'
import {frontConfig} from '../settings/frontconfig'

class Nav extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            menu: []
        };
    }
    fetchMenu = async() => {
        await axios
            .get(frontConfig.mainRestRef + frontConfig.mainJsonStream + frontConfig.getPrimaryMenu)
            .then((result) => {
                this.setState({isLoaded: true, menu: result.data});
                this.setState({location: window.location.pathname})
            }, (error) => {
                this.setState({isLoaded: false, error});
            })

    }
    buildListedItems = (p) => {
     
        return p
            .items
            .map((item, i) => {
                if (item.url === this.state.location && item.url !== '/contact') {
                    return (
                        <li key={i}>
                            <a className="active" id={item.title} href={item.url}>{item.title}</a>
                        </li>
                    )
                } 
                else if(item.url === '/contact' && item.url === this.state.location){
                    return <li key={i}>
                            <a className="btn text-white" style={{backgroundColor:"#5EBDDB"}} id={item.title} href={item.url}>{item.title}</a>
                    </li>
                }
                else if(item.url === '/contact'){
                    return <li key={i}>
                            <a className="btn text-white" id={item.title} href={item.url}>{item.title}</a>
                    </li>
                }
                else if(item.url !== this.state.location) {
                    return (
                        <li key={i}>
                            <a id={item.title} href={item.url}>{item.title}</a>
                        </li>
                    )
                }

            })

    }
    componentDidMount() {
        this.fetchMenu();
    }
    render() {

        const {error, isLoaded, menu} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return ('');
        } else {
            return <section
                className="middle container d-flex align-items-center justify-content-between border-black border-bottom">
                <div>
                    <a href="/">
                        <img src='https://beheer.isoftmix.be/wp-content/uploads/2020/05/Logo-ISOFTMIX-1.png' width={250}></img >
                    </a>
                </div>
                <nav className="navbar navbar-expand">
                    <ul className="navbar-nav mr-auto d-flex align-items-center flex-nowrap">
                        {this.buildListedItems(menu)}
                    </ul >
                </nav>
            </section>
        }
    }
}

export default Nav;