import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import React from 'react';
import ScrollAnimation from 'react-animate-on-scroll';
import axios from 'axios'
import {faAngleDoubleRight} from '@fortawesome/free-solid-svg-icons';
import {frontConfig} from '../settings/frontconfig'

class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            data: []
        };
    }

    componentDidMount = () => {
        this.fetch();
    }

    fetch = () => {
        axios
            .get(frontConfig.mainRestRef + frontConfig.mainJsonStream + frontConfig.getSections)
            .then((result) => {
                this.setState({isLoaded: true, data: result.data[2].acf});
                console.log(result)
            }, (error) => {
                this.setState({isLoaded: false, error});
            })
    }

    render() {

        const {data} = this.state;
       
        return <section className="about">
            <div className="container d-flex">
                <div className="d-flex flex-column align-items-start w-100 py-5 flex-wrap">
                <ScrollAnimation animateIn="slideInLeft">
                    <div className="d-flex flex-row align-items-center">
                            <span className="mr-3"> <a href="/about"><FontAwesomeIcon style={{fontSize: '1.4em',color: '#FFBF37'}} icon={faAngleDoubleRight}/></a></span>
    <span className="mr-3"><a href="/about"> <h2>{data.title_nl_1}</h2></a></span>
                    </div>
                    </ScrollAnimation>
                    <div className="d-flex flex-row flex-wrap">
                       
                        <div className="col-md-6">
                        <ScrollAnimation animateIn="slideInLeft">
                            <p>{data.paragraaf_nl}</p>
                        </ScrollAnimation>
                        </div>
                        
                        <div className="col-md-6">
                        <ScrollAnimation animateIn="slideInRight">
                            <img className="img-fluid" src={data.pic}></img>
                        </ScrollAnimation>
                        </div>
                       
                    </div>
             
                </div>
            </div>
        </section>;

    }
}

export default About;