//Assets
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import 'bootstrap/dist/css/bootstrap.min.css'
import './assets/css/main.min.css'
import "animate.css/animate.min.css"

import {BrowserRouter, Link, Route, Switch} from 'react-router-dom';

import About from './pages/About'
import Contact from './pages/Contact'
import ContactSucces from './pages/ContactSucces'
import CookieConsent from "react-cookie-consent";
import Cookies from './pages/Cookies'
import Error from './pages/Error';
import {Helmet} from 'react-helmet';
import Home from './pages/Home'
import LoaderComponent from './components/Loader'
import PrivacyPolicy from './pages/PrivacyPolicy'
import ProductDetail from './pages/ProductDetail'
import Producten from "./pages/Products";
import React from 'react';
import ReactGA from 'react-ga';
import SitemapPage from './pages/Sitemap'
import Testimonials from './pages/Testimonials'
import Voorwaarden from './pages/TermsOfService'
import axios from 'axios'
import {frontConfig} from './settings/frontconfig'

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoadedMain: false,
            isLoadedProducts: false,
            isLoadedMenu0: [],
            isLoadedMenu1: [],
            isLoadedMenu2: [],
            data: [],
            products: [],
            menu0: [],
            menu1: [],
            menu2: []
        };
    }
    componentDidMount = () => {
        this.fetchData();
        ReactGA.initialize('UA-166939824-1');
        ReactGA.pageview(window.location.pathname + window.location.search);
    }

    fetchData = async () => {
        await axios
            .get(frontConfig.mainRestRef + frontConfig.mainJsonStream)
            .then((result) => {
                this.setState({isLoadedMain: true, data: result.data});
            }, (error) => {
                this.setState({isLoadedMain: false, error});
            })
        await axios
            .get(frontConfig.mainRestRef + frontConfig.mainJsonStream + frontConfig.getProducts)
            .then((result) => {
                this.setState({isLoadedProducts: true, products: result.data});
            }, (error) => {
                this.setState({isLoadedProducts: false, error});
            })
    }

    render() {
        const {error,isLoadedMain,isLoadedProducts,data,products,} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (isLoadedMain === true && isLoadedProducts === true) {
            return (
            <> 
            <Helmet>
                <title>{data.name} - {data.description}</title >
                <meta name="description" content={data.description}/>
                <meta charset="UTF-8"/>
                <meta name="description" content="Free Web tutorials"/>
                <meta name="keywords" content="HTML, CSS, JavaScript"/>
                <meta name="author" content="methods - Alfano Angelo"/>
            </Helmet> 
            <BrowserRouter> 
            {/* {TODO: MAP ALL ROUTES FROM MENU ITEMS} */}
            {/* {TODO: PASS ALL DATA FROM HERE} */}
            <Switch> 
                <Route exact path="/" render={() => <Home/>} />
                <Route exact path='/about' component={About}/>
                <Route exact path='/producten' component={Producten}/>
                <Route exact path='/contact' component={Contact}/>
                <Route exact path='/contact/succes' component={ContactSucces}/>
                <Route exact path='/testimonials' component={Testimonials}/>
                <Route exact path='/sitemap' component={SitemapPage}/>
                <Route exact path='/privacy-policy' component={PrivacyPolicy}/>
                <Route exact path='/cookies' component={PrivacyPolicy}/>
                <Route exact path='/voorwaarden' component={Voorwaarden}/>
                <Route path='/admin' component={() => {  window.location.href = frontConfig.mainRestRef + '/wp-admin'; return null;}}/>
                {products.map((product, i) => {
                    return <Route exact key={i} path={'/producten/' + product.acf.permalink} component={ProductDetail}/>
                })}
                <Route component={Error}/>
            </Switch> 
            </BrowserRouter>
            <CookieConsent buttonText="Ik begrijp het!" cookieName="GDPR_COOKIE" expires={150} buttonStyle={{ backgroundColor: "#FFBF37", fontSize: "13px",borderRadius:"4px" }}>
                Deze website maakt gebruik van cookies om uw ervaring te verbeteren. Klik <a href='/privacy-policy'>hier</a> voor meer info.
            </CookieConsent > 
            </>)
        } else {
            return (<LoaderComponent/>);
        }
    }
}

export default App;