import {slide as Menu} from 'react-burger-menu'
import React from 'react';
import axios from 'axios'
import {frontConfig} from '../settings/frontconfig'
import logo from '../assets/svg/logo.svg'

class Burger extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            menuOpen: false,
            error: null,
            isLoaded: false,
            menu: [],
        }
    }
    componentDidMount(){
        this.fetchMenu();
    }
    handleStateChange(state) {
        this.setState({menuOpen: state.isOpen})
    }
    closeMenu() {
        this.setState({menuOpen: false})
    }
    toggleMenu() {
        this.setState(state => ({
            menuOpen: !state.menuOpen
        }))
    }
    fetchMenu = async() => {
        await axios
            .get(frontConfig.mainRestRef + frontConfig.mainJsonStream + frontConfig.getPrimaryMenu)
            .then((result) => {
                this.setState({isLoaded: true, menu: result.data});
                this.setState({location: window.location.pathname})
            }, (error) => {
                this.setState({isLoaded: false, error});
            })

    }
    buildListedItems = (p) => {
        return p
            .items
            .map((item, i) => {
                if (item.url === this.state.location) {
                    return (
                        <li key={i}>
                            <a className="active nav-item" id={item.title} href={item.url}>{item.title}</a>
                        </li>
                    )
                } else {
                    return (
                        <li key={i}>
                            <a className="nav-item" id={item.title} href={item.url}>{item.title}</a>
                        </li>
                    )
                }

            })

    }
    render() {
        const {error, isLoaded, menu} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return ('');
        } else {
        return (<><div className="container d-flex justify-content-center my-5">
                    <a href="/">
                        <img src='https://isoftmix-be.preview-domain.com/wp-content/uploads/2020/05/Logo-ISOFTMIX.png' width={250}></img >
                    </a>
                </div>
            <div id="outer-container" onClick={() => this.closeMenu()}>
                                
            <Menu 
                    pageWrapId={"page-wrap"}
                    outerContainerId={"outer-container"}
                    isOpen={this.state.menuOpen}
                    onStateChange={(state) => this.handleStateChange(state)}>
                    <nav id="page-wrap" onClick={() => this.closeMenu()}>
                        <ul>
                            {this.buildListedItems(menu)}
                        </ul>
                    </nav>
                    </Menu>
            </div></>
        ); 
    }

    }
}

export default Burger;