import AnchorLink from 'react-anchor-link-smooth-scroll'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import React from 'react';
import axios from 'axios'
import {faFacebookSquare} from '@fortawesome/free-brands-svg-icons';
import {faInstagram} from '@fortawesome/free-brands-svg-icons';
import {faLinkedinIn} from '@fortawesome/free-brands-svg-icons';
import {faMapMarkerAlt} from '@fortawesome/free-solid-svg-icons';
import {faPhoneAlt} from '@fortawesome/free-solid-svg-icons';
import {frontConfig} from '../settings/frontconfig'

class Topbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            data: []
        };
    }
    componentDidMount() {
        this.fetch();
    }
    fetch = async () => {
        await axios
            .get(frontConfig.mainRestRef + frontConfig.mainJsonStream + frontConfig.getSections)
            .then((result) => {
                this.setState({isLoaded: true, data: result.data[1].acf});
            }, (error) => {
                this.setState({isLoaded: false, error});
            })
    }
    render() {
        const {error, isLoaded, data} = this.state;
        return (
            <section>
                <div className="topbar container-fluid d-flex align-items-center">
                    <div className="container d-flex justify-content-between">
                        <div>
                            <span className="mr-3"><FontAwesomeIcon icon={faMapMarkerAlt}/>
                          <AnchorLink
                                    offset='100'
                                    href='#googlemap'>{data.adres}
                                </AnchorLink >
                            </span>
                            <span><FontAwesomeIcon icon={faPhoneAlt}/>
                                <a href={'tel:'+data.telefoon_nummer}>{data.telefoon_nummer}</a>
                            </span>
                        </div>
                        <div>
                            <span>
                                <a href={data.facebook}><FontAwesomeIcon icon={faFacebookSquare}/>Facebook</a>
                            </span>
                            <span>
                                <a href={data.linkedin}><FontAwesomeIcon icon={faInstagram}/>Instagram</a>
                            </span>
                            <span>
                                <a href={data.instagram}><FontAwesomeIcon icon={faLinkedinIn}/>linkedin</a>
                            </span>
                        </div>
                        {/* <div className="langswitcher">
                          <span><a href="/" className="active">NL</a> |</span>
                          <span><a>EN</a> |</span>
                          <span><a>FR</a> |</span>
                          <span><a>DU</a></span>
                        </div> */}
                    </div>
                </div>
            </section>
        );
    }
    
}

export default Topbar;